import { IProduct } from '../reducers/products'

export default function formatProductPayload(data: any): IProduct {
	return {
		id: data.id,
		name: data.name,
		shortName: data.recipe.type.shortName,
		price: data.price,
		volume: data.volumeLiters,
		image: data.iconSrc,
		color: `#${data.recipe.type.color}`,
		ingredients: data.recipe.ingredients.map(
			(ingredient: any) => ingredient.name,
		),
	}
}



