import React from 'react'
import styles from './styles.sass'
import { IBasketItems } from '../../actions/basket'
import { IProduct } from '../../reducers/products'
import formatIngredients from '../../utils/formatIngredients'
import Bottle from '../Bottle'
import cn from 'classnames'
import formatVolumePrice from '../../utils/formatVolumePrice'
import isProductSmall from '../../utils/isProductSmall'

export interface IBasketTunerProps {
	items: IBasketItems
}

export interface IBasketTunerActionProps {
	addToBasket: (product: IProduct) => void
	removeFromBasket: (product: IProduct) => void
}

interface IProps extends IBasketTunerProps, IBasketTunerActionProps {}

export default class BasketTuner extends React.Component<
	IProps
> {

	render() {
		const { items, addToBasket, removeFromBasket } = this.props

		return (
			<div className={styles.wrapper}>
				{Object.values(items).map((item) => (
					<div className={styles.item} key={item.product.id}>
						<div className={cn(styles.image, isProductSmall(item.product) && styles.viewSmall)} style={{
							color: item.product.color,
						}}>
							<Bottle className={styles.imageIn} />
						</div>
						<div className={styles.info}>
							<div className={styles.title}>
								{item.product.name}
							</div>
							<div className={styles.price}>
								{formatVolumePrice(item.product.volume, item.product.price)}
							</div>
							<div className={styles.note}>
								{formatIngredients(item.product.ingredients)}
							</div>
						</div>
						<div className={styles.controls}>
							<button
								className={styles.remove}
								aria-label="Remove"
								role="button"
								onClick={() => removeFromBasket(item.product)}
							><i className="fas fa-minus"></i></button>
							<div className={styles.quantity}>
								{item.quantity}
							</div>
							<button
								className={styles.add}
								aria-label="Add"
								role="button"
								onClick={() => addToBasket(item.product)}
							><i className="fas fa-plus"></i></button>
						</div>
					</div>
				))}
			</div>
		)
	}
}



