export default function smoothHorizontalScroll(wrapper: HTMLElement, position: number) {
	if (wrapper.scroll) {
		wrapper.scroll({
			left: position,
			behavior: 'smooth',
		})
	} else {
		wrapper.scrollLeft = position
	}
}



