import createActionCreator from '../utils/createActionCreator'
import { Dispatch } from 'redux'

export const SET_OFFLINE = 'SET_OFFLINE'
const offline = createActionCreator(SET_OFFLINE, () => {})
export const setOffline = () => (dispatch: Dispatch<any>) => {
	dispatch(offline({}))
}

export const UNSET_OFFLINE = 'UNSET_OFFLINE'
const online = createActionCreator(UNSET_OFFLINE, () => {})
export const setOnline = () => (dispatch: Dispatch<any>) => {
	dispatch(online({}))
}



