import React from 'react'
import translate from '../../utils/translate'
import logo from './logo.svg'
import styles from './styles.sass'
import PasswordReset from '../../containers/PasswordResetContainer'
import Loading from '../Loading'
import { AnalyticsView, AnalyticsEventCategory, AnalyticsEventAction } from '../../models/analytics'
import setAnalyticsView from '../../utils/setAnalyticsView'
import submitAnalyticsEvent from '../../utils/submitAnalyticsEvent'

export interface ILoginProps {
	loading: boolean
}

export interface ILoginActionProps {
	loginWithEmailPassword: (email: string, password: string) => void
}

export interface ILoginState {
	email: string
	password: string
	resetPasswordActive: boolean
}

export default class Login extends React.Component<
	ILoginProps & ILoginActionProps,
	ILoginState
> {
	state: ILoginState = {
		email: '',
		password: '',
		resetPasswordActive: false,
	}

	render() {
		const {
			email,
			password,
			resetPasswordActive,
		} = this.state
		const { loading } = this.props

		return (
			<div className={styles.wrapper}>
				<img src={logo} width="94" height="34" alt="Logo" />
				<form onSubmit={this.onSubmit.bind(this)}>
					<label>
						{translate('login.loginEmail')}
						<br />
						<input
							type="email"
							name="email"
							autoComplete="email"
							value={email}
							disabled={loading}
							onChange={this.onEmailChange.bind(this)}
						/>
					</label>
					<br />
					<br />
					<br />
					<label>
						{translate('login.loginPassword')}
						<br />
						<input
							type="password"
							name="password"
							autoComplete="current-password"
							value={password}
							disabled={loading}
							onChange={this.onPasswordChange.bind(this)}
						/>
					</label>
					<br />
					<br />
					<br />
					<button type="submit" disabled={loading}>
						{translate('login.doLogin')}
						<i className="fas fa-arrow-right"></i>
					</button>
				</form>
				<PasswordReset
					isActive={resetPasswordActive}
					onClose={this.closeResetPassword}
					prefilledEmail={email}
				/>
				<button role="button" onClick={this.openResetPassword}>
					{translate('login.forgottenPwd')}
				</button>
				<div className={styles.loading}>
					<Loading isActive={loading} />
				</div>
			</div>
		)
	}

	private onEmailChange(event: React.FormEvent<HTMLInputElement>) {
		this.setState({ email: event.currentTarget.value })
	}

	private onPasswordChange(event: React.FormEvent<HTMLInputElement>) {
		this.setState({ password: event.currentTarget.value })
	}

	private onSubmit(event: React.FormEvent<HTMLFormElement>) {
		event.preventDefault()

		const { email, password } = this.state

		if (!this.props.loading) {
			this.props.loginWithEmailPassword(email, password)
			submitAnalyticsEvent(AnalyticsEventCategory.User, AnalyticsEventAction.Login)
		}
	}

	private openResetPassword = () => {
		this.setState({ resetPasswordActive: true })

		setAnalyticsView(AnalyticsView.PasswordReset)
	}

	private closeResetPassword = () => {
		this.setState({ resetPasswordActive: false })

		setAnalyticsView(AnalyticsView.Login)
	}
}



