import ReactGA from 'react-ga'
import getVersion from './getVersion'

// @ts-ignore
ReactGA.initialize('UA-120657426-1', {
	debug: DEBUG,
	gaOptions: {
		appName: 'Goodlok',
	},
})
ReactGA.pageview(window.location.pathname)
ReactGA.set({ appVersion: getVersion() })



