import { connect } from 'react-redux'
import { IState } from '../reducers'
import RecentOrder, { IRecentOrderActionProps } from '../components/RecentOrder'
import {
	updateRecentlyFinishedOrder,
	dismissRecentlyFinishedOrder,
} from '../actions/orders'

const mapStateToProps = (state: IState) => ({
	order: state.orders.recentlyFinishedOrder,
})
const mapDispatchToProps: IRecentOrderActionProps = {
	updateRecentlyFinishedOrder,
	dismissRecentlyFinishedOrder,
}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(RecentOrder)



