import React from 'react'
import translate from '../../utils/translate'
import Modal from '../Modal'
import { IOrderSkeleton } from '../../models/order'
import styles from './styles.sass'
import BasketItems from '../BasketItems'

export interface IRecentOrderOwnProps {
}

export interface IRecentOrderProps {
	order: IOrderSkeleton
}

export interface IRecentOrderActionProps {
	updateRecentlyFinishedOrder: (order: IOrderSkeleton) => void
	dismissRecentlyFinishedOrder: () => void
}

interface IState {
	order: IOrderSkeleton
}

interface IProps extends IRecentOrderOwnProps, IRecentOrderProps, IRecentOrderActionProps {}

export default class RecentOrder extends React.Component<
	IProps, IState
> {
	state: IState = {
		order: this.props.order,
	}

	componentWillReceiveProps(nextProps: IProps) {
		if (nextProps.order) {
			this.setState({
				order: nextProps.order,
			})
		}
	}

	render() {
		const isActive = Boolean(this.props.order)
		const { order } = this.state

		return (
			<Modal
				isActive={isActive}
				onClose={this.onClose}
				title={translate('orderFinished.thanks')}
			>
				{order && (
					<div className={styles.wrapper}>
						<div className={styles.summary}>
							<div className={styles.address}>
								{translate('addresses.addressloc')}: <b>{order.addressTitle}</b>
							</div>
							<div className={styles.date}>
								Date: <b>{order.deliverAt}</b>
							</div>
							<div className={styles.items}>
								<BasketItems items={order.products} />
							</div>
						</div>
						<div className={styles.more}>
							<button
								role="button"
								onClick={this.onClose}
								className={styles.moreAction}
							>
								{translate('orderFinished.orderMore')}
							</button>
						</div>
					</div>
				)}
			</Modal>
		)
	}

	private onClose = () => {
		this.props.dismissRecentlyFinishedOrder()
	}
}



