import createActionCreator, {
	IStandardAction,
} from '../utils/createActionCreator'
import { Dispatch } from 'redux'
import { IAddress } from '../reducers/addresses'
import api from '../utils/api'
import { setDefaultAddress } from './user'
import { IState } from '../reducers'

export interface IAddresses {
	[id: string]: IAddress
}

export const SET_ADDRESSES = 'SET_ADDRESSES'
export type SetAddressesAction = IStandardAction<IAddresses>
const setAddresses = createActionCreator(
	SET_ADDRESSES,
	(addresses: IAddresses) => addresses,
)
export const requestAddresses = () => (dispatch: Dispatch<any>) => {
	dispatch(setAddressesLoading({}))
	api.requestAddresses().then((data) => {
		dispatch(setAddresses(data))
	})
	.catch(() => {})
	.then(() => {
		dispatch(unsetAddressesLoading({}))
	})
}

export const ADD_ADDRESS = 'ADD_ADDRESS'
export type AddAddressAction = IStandardAction<IAddress>
const add = createActionCreator(
	ADD_ADDRESS,
	(address: IAddress) => address,
)
export const addAddress = (title: string, address: string) => (
	dispatch: Dispatch<any>,
) => {
	dispatch(setAddressesLoading({}))
	api.addAddress(title, address).then((data) => {
		dispatch(add(data))
		dispatch(setDefaultAddress(data))
	})
	.catch(() => {})
	.then(() => {
		dispatch(unsetAddressesLoading({}))
	})
}

export const REMOVE_ADDRESS = 'REMOVE_ADDRESS'
export type RemoveAddressAction = IStandardAction<IAddress>
const remove = createActionCreator(REMOVE_ADDRESS, (address: IAddress) => address)
export const removeAddress = (address: IAddress) => (dispatch: Dispatch<any>, getState: () => IState) => {
	dispatch(remove(address))
	api.removeAddress(address).then(() => {
		const state = getState()

		if (state.user.defaultAddress && state.user.defaultAddress.id === address.id) {
			dispatch(setDefaultAddress(null))
		}
	})
	.catch(() => {
		dispatch(add(address))
	})
}

interface IEditAddress {
	oldAddress: IAddress
	newAddress: IAddress
}
export const EDIT_ADDRESS = 'EDIT_ADDRESS'
export type EditAddressAction = IStandardAction<IEditAddress>
const edit = createActionCreator(
	EDIT_ADDRESS,
	(addresses: IEditAddress) => addresses,
)
export const editAddress = (address: IAddress) => (
	dispatch: Dispatch<any>, getState: () => IState,
) => {
	const state = getState()

	dispatch(setAddressesLoading({}))

	Promise.all([
		api.removeAddress(address),
		api.addAddress(address.title, address.address),
	])
		.then((values) => {
			const newAddress = values[1]

			dispatch(edit({
				newAddress,
				oldAddress: address,
			}))

			if (state.user.defaultAddress && state.user.defaultAddress.id === address.id) {
				dispatch(setDefaultAddress(newAddress))
			}
		})
		.catch(() => {})
		.then(() => {
			dispatch(unsetAddressesLoading({}))
		})
}

export const SET_ADDRESSES_LOADING = 'SET_ADDRESSES_LOADING'
const setAddressesLoading = createActionCreator(
	SET_ADDRESSES_LOADING,
	() => ({}),
)

export const UNSET_ADDRESSES_LOADING = 'UNSET_ADDRESSES_LOADING'
const unsetAddressesLoading = createActionCreator(
	UNSET_ADDRESSES_LOADING,
	() => ({}),
)

export const UNSET_ADDRESSES = 'UNSET_ADDRESSES'
const unset = createActionCreator(UNSET_ADDRESSES, () => {})
export const unsetAddresses = () => (dispatch: Dispatch<any>) => {
	dispatch(unset({}))
}



