import createActionCreator, {
	IStandardAction,
} from '../utils/createActionCreator'
import { Dispatch } from 'redux'
import { IPromotion } from '../reducers/promotions'
import api from '../utils/api'

export interface IPromotions {
	[id: string]: IPromotion
}

export const SET_PROMOTIONS = 'SET_PROMOTIONS'
export type SetPromotionsAction = IStandardAction<IPromotions>
const setPromotions = createActionCreator(
	SET_PROMOTIONS,
	(promotions: IPromotions) => promotions,
)
export const requestPromotions = () => (dispatch: Dispatch<any>) => {
	api.requestPromotions().then((data) => {
		dispatch(setPromotions(data))
	})
}

export const UNSET_PROMOTIONS = 'UNSET_PROMOTIONS'
const unset = createActionCreator(UNSET_PROMOTIONS, () => {})
export const unsetPromotions = () => (dispatch: Dispatch<any>) => {
	dispatch(unset({}))
}



