import React from 'react'
import styles from './styles.sass'
import Popup from '../Popup'
import translate from '../../utils/translate'
import { IOrders } from '../../models/order'

export interface IOrdersOwnProps {
	isActive: boolean
	onClose: () => void
}

export interface IOrdersProps {
	orders: IOrders
}

export interface IOrdersActionProps {
	requestOrders: () => void
}

export interface IProps extends IOrdersOwnProps, IOrdersProps, IOrdersActionProps {}

interface IState {}

export default class Addresses extends React.Component<
	IProps, IState
> {
	componentWillMount() {
		this.props.requestOrders()
	}

	render() {
		const {
			orders,
			isActive,
			onClose,
		} = this.props

		return (
			<Popup
				isActive={isActive}
				onClose={onClose}
				title={translate('menu.repeating.title')}
			>
				<div className={styles.wrapper}>
					<div className={styles.list}>
						{Object.values(orders).map((order) => (
							<section
								key={order.id}
							>
								{JSON.stringify(order, null, 2)}
							</section>
						))}
					</div>
				</div>
			</Popup>
		)
	}
}



