import { connect } from 'react-redux'
import { requestPromotions } from '../actions/promotions'
import { IState } from '../reducers'
import Promotions, {
	IPromotionsProps,
	IPromotionsActionProps,
} from '../components/Promotions'

const mapStateToProps = (state: IState) => ({
	loading: state.promotions.loading,
	promotions: state.promotions.items,
})
const mapDispatchToProps = { requestPromotions }

export default connect<IPromotionsProps, IPromotionsActionProps>(
	mapStateToProps,
	mapDispatchToProps,
)(Promotions)



