import React from 'react'
import { render } from 'react-dom'
import { Provider } from 'react-redux'
import { AppContainer } from 'react-hot-loader'
import './utils/initializeGoogleAnalytics'
import './utils/initializeSmoothScrollPolyfill'
import configureStore from './configureStore'
import App from './containers/AppContainer'
import api from './utils/api'
import * as OfflinePluginRuntime from 'offline-plugin/runtime'

// Install service worker
if (!DEBUG) {
	OfflinePluginRuntime.install()
}

const store = configureStore()

// Connect api
api.setStore(store)

// tslint:disable-next-line:variable-name
function renderMain(App: React.ReactType) {
	return (
		<AppContainer>
			<Provider store={store}>
				<App />
			</Provider>
		</AppContainer>
	)
}

render(renderMain(App), document.getElementById('root'))

if (module.hot) {
	module.hot.accept('./containers/AppContainer', () => {
		render(
			renderMain(require('./containers/AppContainer').default),
			document.getElementById('root'),
		)
	})
}



