import handleActions from '../utils/handleActions'
import {
	SET_ADDRESSES,
	SetAddressesAction,
	ADD_ADDRESS,
	AddAddressAction,
	REMOVE_ADDRESS,
	RemoveAddressAction,
	EDIT_ADDRESS,
	EditAddressAction,
	IAddresses,
	SET_ADDRESSES_LOADING,
	UNSET_ADDRESSES_LOADING,
	UNSET_ADDRESSES,
} from '../actions/addresses'

export interface IAddressesState {
	loading: boolean,
	items: IAddresses,
}

export interface IAddress {
	id: string
	title: string
	address: string
}

const initialState: IAddressesState = {
	loading: false,
	items: {},
}

// tslint:disable:function-name
export default handleActions(
	{
		[SET_ADDRESSES](state, { payload }: SetAddressesAction) {
			return {
				...state,
				items: { ...payload },
			}
		},
		[ADD_ADDRESS](state, { payload }: AddAddressAction) {
			return {
				...state,
				items: {
					...state.items,
					[payload.id]: payload,
				},
			}
		},
		[REMOVE_ADDRESS](state, { payload }: RemoveAddressAction) {
			const newState = {
				...state,
				items: { ...state.items },
			}
			delete newState.items[payload.id]
			return newState
		},
		[EDIT_ADDRESS](state, { payload }: EditAddressAction) {
			const newState = {
				...state,
				items: { ...state.items },
			}
			if (payload.oldAddress.id !== payload.newAddress.id) {
				delete newState.items[payload.oldAddress.id]
			}
			newState.items[payload.newAddress.id] = payload.newAddress
			return newState
		},
		[SET_ADDRESSES_LOADING](state) {
			return {
				...state,
				loading: true,
			}
		},
		[UNSET_ADDRESSES_LOADING](state) {
			return {
				...state,
				loading: false,
			}
		},
		[UNSET_ADDRESSES](state) {
			return {
				...state,
				items: {},
			}
		},
	},
	initialState,
)



