import handleActions from '../utils/handleActions'
import {
	ADD_BASKET_ITEM,
	AddBasketItemAction,
	REMOVE_BASKET_ITEM,
	RemoveBasketItemAction,
	EMPTY_BASKET,
} from '../actions/basket'
import { IProduct } from './products'

export interface IBasketItem {
	product: IProduct
	quantity: number
}

export interface IBasketState {
	[id: string]: IBasketItem
}

const initialState: IBasketState = {}

// tslint:disable:function-name
export default handleActions(
	{
		[ADD_BASKET_ITEM](state, { payload }: AddBasketItemAction) {
			const newState = {
				...state,
			}

			if (newState[payload.id]) {
				newState[payload.id].quantity += 1
			} else {
				newState[payload.id] = {
					product: payload,
					quantity: 1,
				}
			}
			return newState
		},
		[REMOVE_BASKET_ITEM](state, { payload }: RemoveBasketItemAction) {
			const newState = {
				...state,
			}

			if (newState[payload.id]) {
				newState[payload.id].quantity -= 1

				if (newState[payload.id].quantity === 0) {
					delete newState[payload.id]
				}
			}
			return newState
		},
		[EMPTY_BASKET]() {
			return {}
		},
	},
	initialState,
)



