import { connect } from 'react-redux'
import { registerWithEmailPassword } from '../actions/user'
import { IState } from '../reducers'
import Registration, { IRegistrationActionProps } from '../components/Registration'

const mapStateToProps = (state: IState) => ({ loading: state.user.loading })
const mapDispatchToProps: IRegistrationActionProps = { registerWithEmailPassword }

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(Registration)



