import React from 'react'
import styles from './styles.sass'
import cn from 'classnames'

export interface IOfflineProps {
	offline: boolean
}

export interface IOfflineActionProps {}

interface IProps extends IOfflineProps, IOfflineActionProps {}

export default class Offline extends React.Component<
	IProps
> {
	render() {
		const { offline } = this.props

		return (
			<div className={cn(styles.wrapper, offline && styles.isOffline)}>
				<div className={styles.icon}>
					<i className="fas fa-wifi" aria-label="Offline"></i>
				</div>
			</div>
		)
	}
}



