import React from 'react'
import styles from './styles.sass'
import cn from 'classnames'

export interface IModalOwnProps {
	isActive: boolean
	onClose: () => void
	title?: string
}

export default class Modal extends React.Component<
	IModalOwnProps
> {
	render() {
		const { isActive, onClose, children, title } = this.props

		return (
			<section className={cn(styles.wrapper, isActive && styles.isActive)}>
				<div className={styles.in}>
					<div className={styles.head}>
						{title && (
							<h1 className={styles.title}>
								{title}
							</h1>
						)}
						<button
							role="button"
							onClick={onClose}
							className={styles.close}
							aria-label="Close"
						>
						<i className="fas fa-times"></i>
						</button>
					</div>
					<div className={styles.content}>
						{children}
					</div>
				</div>
				<button
					role="button"
					className={styles.backgroundClose}
					aria-label="Close"
					onClick={onClose}
				/>
			</section>
		)
	}
}



