import { connect } from 'react-redux'
import { requestProducts } from '../actions/products'
import { IState } from '../reducers'
import Offer, { IOfferProps, IOfferActionProps } from '../components/Offer'

const mapStateToProps = (state: IState) => ({
	loading: state.products.loading,
	products: state.products.items,
})
const mapDispatchToProps = { requestProducts }

export default connect<IOfferProps, IOfferActionProps>(
	mapStateToProps,
	mapDispatchToProps,
)(Offer)



