import React from 'react'
import styles from './styles.sass'
import BottomBar from '../BottomBar'
import cn from 'classnames'
import CollapseFromTop from '../CollapseFromTop'

export interface IPopupOwnProps {
	isActive: boolean
	onClose: () => void
	bottomBar?: JSX.Element
	title?: string
}

export default class Popup extends React.Component<
	IPopupOwnProps
> {
	render() {
		const { isActive, onClose, children, bottomBar, title } = this.props

		return (
			<section className={cn(styles.wrapper, isActive && styles.isActive)}>
				<div className={styles.head}>
					<CollapseFromTop onClick={onClose} />
					{title && (
						<h1 className={styles.title}>
							{title}
						</h1>
					)}
				</div>
				<div className={styles.content}>
					{children}
				</div>
				{bottomBar && <BottomBar isActive={true}>
					{bottomBar}
				</BottomBar>}
			</section>
		)
	}
}



