import { connect } from 'react-redux'
import { IState } from '../reducers'
import Offline, {
	IOfflineActionProps,
} from '../components/Offline'

const mapStateToProps = (state: IState) => ({
	offline: state.general.offline,
})
const mapDispatchToProps: IOfflineActionProps = {}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(Offline)



