import React from 'react'
import { IProducts } from '../../actions/products'
import Products from '../Products'

export interface IOfferProps {
	loading: boolean
	products: IProducts
}

export interface IOfferActionProps {
	requestProducts: () => void
}

export default class Offer extends React.Component<
	IOfferProps & IOfferActionProps
> {
	componentWillMount() {
		this.props.requestProducts()
	}

	render() {
		const { loading, products } = this.props

		return <Products products={loading ? undefined : products} />
	}
}



