const dayNames = [
	'Sunday',
	'Monday',
	'Tuesday',
	'Wednesday',
	'Thursday',
	'Friday',
	'Saturday',
]

function doubleDigit(input: number) {
	if (input < 10) {
		return `0${input}`
	}
	return `${input}`
}

function tommorow(input: Date) {
	const output = new Date(input)
	output.setDate(input.getDate() + 1)
	return output
}

function hoursAndMinutes(date: Date) {
	return `${date.getHours()}:${doubleDigit(date.getMinutes())}`
}

function dayInWeek(date: Date): string {
	return dayNames[date.getDay()]
}

export default function formatDateToLabel(date: string): string {
	const target = new Date(date)
	const now = new Date()

	now.setDate(now.getDate() - 5)

	const targetDaysCrop = new Date(target).setHours(0, 0, 0, 0)
	const nowDaysCrop = new Date(now).setHours(0, 0, 0, 0)
	const nowDaysTommorowCrop = tommorow(now).setHours(0, 0, 0, 0)

	if (targetDaysCrop === nowDaysCrop) {
		return hoursAndMinutes(target)
	}
	if (targetDaysCrop === nowDaysTommorowCrop) {
		return `Tomorrow ${hoursAndMinutes(target)}`
	}
	return `${dayInWeek(target)} ${hoursAndMinutes(target)}`
}



