import createActionCreator, {
	IStandardAction,
} from '../utils/createActionCreator'
import { Dispatch } from 'redux'
import api from '../utils/api'
import { IDate } from '../models/date'
import { IBasketItems, emptyBasket } from './basket'
import { IAddress } from '../reducers/addresses'
import { paymentMethod } from '../models/payment'
import { IOrders, IOrderSkeleton } from '../models/order'
import submitAnalyticsEvent from '../utils/submitAnalyticsEvent'
import { AnalyticsEventAction, AnalyticsEventCategory } from '../models/analytics'

export interface IDeliveryWindow {
	duration: string
	query: string
	date: IDate
}

export const SET_DELIVERY_WINDOW = 'SET_DELIVERY_WINDOW'
export type SetDeliveryWindowAction = IStandardAction<IDeliveryWindow>
const setDeliveryWindow = createActionCreator(
	SET_DELIVERY_WINDOW,
	(deliveryWindow: IDeliveryWindow) => deliveryWindow,
)
export const requestDeliveryWindow = () => (
	dispatch: Dispatch<any>,
) => {
	api.requestNextDeliveryWindow()
		.then((data) => {
			dispatch(setDeliveryWindow(data))
		})
}

export const SET_ORDER_LOADING = 'SET_ORDER_LOADING'
const setOrderLoading = createActionCreator(
	SET_ORDER_LOADING,
	() => {},
)
export const UNSET_ORDER_LOADING = 'UNSET_ORDER_LOADING'
const unsetOrderLoading = createActionCreator(
	UNSET_ORDER_LOADING,
	() => {},
)
export const placeOrder = (items: IBasketItems, address: IAddress, date: IDate) => (
	dispatch: Dispatch<any>,
) => {
	dispatch(setOrderLoading({}))
	submitAnalyticsEvent(AnalyticsEventCategory.Basket, AnalyticsEventAction.Order)
	api.order(items, address, date, paymentMethod.StoredCard)
		.then((data) => {
			const { gatewayRedirectLink } = data
			if (gatewayRedirectLink) {
				window.location = data.gatewayRedirectLink
			} else {
				dispatch(updateRecentlyFinishedOrder({
					addressTitle: address.title,
					deliverAt: date.label,
					products: items,
				}))
				dispatch(emptyBasket())
			}
		})
		.catch(() => {})
		.then(() => {
			dispatch(unsetOrderLoading({}))
		})
}

export const SET_ORDERS = 'SET_ORDERS'
export type SetOrdersAction = IStandardAction<IOrders>
const setOrders = createActionCreator(
	SET_ORDERS,
	(orders: IOrders) => orders,
)
export const requestOrders = () => (dispatch: Dispatch<any>) => {
	api.requestOrders().then((data) => {
		dispatch(setOrders(data))
	})
}

export const SET_RECENTLY_FINISHED_ORDER = 'SET_RECENTLY_FINISHED_ORDER'
export type SetRecentlyFinishedOrderAction = IStandardAction<IOrderSkeleton>
const setRecentlyFinishedOrder = createActionCreator(
	SET_RECENTLY_FINISHED_ORDER,
	(order: IOrderSkeleton) => order,
)
export const updateRecentlyFinishedOrder = (order: IOrderSkeleton) => (dispatch: Dispatch<any>) => {
	dispatch(setRecentlyFinishedOrder(order))
}

export const UNSET_RECENTLY_FINISHED_ORDER = 'UNSET_RECENTLY_FINISHED_ORDER'
const unsetRecentlyFinishedOrder = createActionCreator(
	UNSET_RECENTLY_FINISHED_ORDER,
	() => {},
)
export const dismissRecentlyFinishedOrder = () => (dispatch: Dispatch<any>) => {
	dispatch(unsetRecentlyFinishedOrder({}))
}

export const UNSET_ORDERS = 'UNSET_ORDERS'
const unset = createActionCreator(UNSET_ORDERS, () => {})
export const unsetOrders = () => (dispatch: Dispatch<any>) => {
	dispatch(unset({}))
}



