import React from 'react'
import TopNavigation, { TopNavigationSection } from '../TopNavigation'
import Settings from '../../containers/SettingsContainer'
import BasketPreview from '../../containers/BasketPreviewContainer'
import Offer from '../../containers/OfferContainer'
import Promotions from '../../containers/PromotionsContainer'
import OrderRecapitulation from '../../containers/OrderRecapitulation'
import styles from './styles.sass'
import { IBasketItems } from '../../actions/basket'
import cn from 'classnames'
import RecentOrder from '../../containers/RecentOrderContainer'
import { AnalyticsView } from '../../models/analytics'
import setAnalyticsView from '../../utils/setAnalyticsView'

export interface ISignedUserProps {
	basketItems: IBasketItems
}

interface IState {
	activeTopNavigation: TopNavigationSection
	activeOrderRecapitulation: boolean
}

export default class SignedUser extends React.Component<ISignedUserProps, IState> {
	state: IState = {
		activeTopNavigation: TopNavigationSection.All,
		activeOrderRecapitulation: false,
	}

	componentDidMount() {
		setAnalyticsView(AnalyticsView[this.state.activeTopNavigation])
	}

	render() {
		const { activeTopNavigation, activeOrderRecapitulation } = this.state
		const { basketItems } = this.props
		const basketPreviewVisible = Object.keys(basketItems).length > 0

		return (
			<div className={styles.wrapper}>
				<div>
					<TopNavigation
						active={activeTopNavigation}
						onChange={this.onTopNavigationChange.bind(this)}
					/>
				</div>
				<div key={activeTopNavigation} className={cn(
					styles.content,
					(basketPreviewVisible || this.isSettingsSectionActive()) && styles.viewBottomBar,
				)}>
					{activeTopNavigation === TopNavigationSection.Now && <Promotions />}
					{activeTopNavigation === TopNavigationSection.All && <Offer />}
					{activeTopNavigation === TopNavigationSection.Settings && <Settings />}
				</div>
				{!this.isSettingsSectionActive() && (
					<React.Fragment>
						<BasketPreview onOpenDetail={this.openOrderRecapitulation.bind(this)} isActive={basketPreviewVisible} />
						<OrderRecapitulation
							onClose={this.closeOrderRecapitulation.bind(this)}
							isActive={activeOrderRecapitulation}
							/>
						<RecentOrder />
					</React.Fragment>
				)}
			</div>
		)
	}

	private isSettingsSectionActive() {
		return this.state.activeTopNavigation === TopNavigationSection.Settings
	}

	private openOrderRecapitulation() {
		this.setState({ activeOrderRecapitulation: true })
		setAnalyticsView(AnalyticsView.OrderRecapitulation)
	}

	private closeOrderRecapitulation() {
		this.setState({ activeOrderRecapitulation: false })
		setAnalyticsView(AnalyticsView[this.state.activeTopNavigation])
	}

	private onTopNavigationChange(activeTopNavigation: TopNavigationSection) {
		this.setState({ activeTopNavigation })
		setAnalyticsView(AnalyticsView[activeTopNavigation])
	}
}



