import handleActions from '../utils/handleActions'
import {
	SET_USER,
	SetUserAction,
	UNSET_USER,
	SET_USER_LOADING,
	SET_DEFAULT_ADDRESS,
	SetDefaultAddressAction,
	SET_DEFAULT_DATE,
	SetDefaultDateAction,
	UNSET_USER_LOADING,
	REMOVE_CARD,
} from '../actions/user'
import { IAddress } from './addresses'
import { IDate } from '../models/date'
import { IPaymentCard } from '../models/payment'
// import ReactGA from 'react-ga'

export interface IUserState {
	id: string
	email: string
	token: string
	loggedIn: boolean
	loading: boolean
	card: null | IPaymentCard
	defaultAddress: null | IAddress
	defaultDate: null | IDate
}

const persistentState: Partial<IUserState> = localStorage.userState && JSON.parse(localStorage.userState)

const emptyState: IUserState = {
	id: '',
	email: '',
	token: '',
	loggedIn: false,
	loading: false,
	card: null,
	defaultAddress: null,
	defaultDate: null,
}
const initialState: IUserState = {
	...emptyState,
	...persistentState,
}

if (initialState.id) {
	analyticsSetUserId(initialState.id)
}

function persistUser(state: IUserState | null) {
	localStorage.userState = JSON.stringify(state)
}

function analyticsSetUserId(id: string) {
	console.log(id)
	// ReactGA.set({ userId: id })
}

function analyticsUnsetUserId() {
	// ReactGA.set({ userId: null })
}

// tslint:disable:function-name
export default handleActions(
	{
		[SET_USER](state, { payload }: SetUserAction) {
			let card = null
			if (payload.cardIssuer) {
				card = {
					issuer: payload.cardIssuer,
					number: payload.cardLast4,
				}
			}
			const newState: IUserState = {
				...state,
				card,
				id: payload.id,
				email: payload.id,
				token: payload.token,
				loggedIn: true,
			}
			persistUser(newState)
			analyticsSetUserId(payload.id)
			return {
				...newState,
				loading: false,
			}
		},
		[UNSET_USER]() {
			persistUser(null)
			analyticsUnsetUserId()
			return emptyState
		},
		[SET_USER_LOADING](state) {
			return {
				...state,
				loading: true,
			}
		},
		[UNSET_USER_LOADING](state) {
			return {
				...state,
				loading: false,
			}
		},
		[SET_DEFAULT_ADDRESS](state, { payload }: SetDefaultAddressAction) {
			const newState: IUserState = {
				...state,
				defaultAddress: payload,
			}
			persistUser(newState)
			return newState
		},
		[SET_DEFAULT_DATE](state, { payload }: SetDefaultDateAction) {
			const newState: IUserState = {
				...state,
				defaultDate: payload,
			}
			persistUser(newState)
			return newState
		},
		[REMOVE_CARD](state) {
			const newState: IUserState = {
				...state,
				card: null,
			}
			persistUser(newState)
			return newState
		},
	},
	initialState,
)



