import handleActions from '../utils/handleActions'
import {
	SET_DELIVERY_WINDOW,
	SetDeliveryWindowAction,
	IDeliveryWindow,
	SET_ORDER_LOADING,
	UNSET_ORDER_LOADING,
	SET_ORDERS,
	SetOrdersAction,
	UNSET_RECENTLY_FINISHED_ORDER,
	SetRecentlyFinishedOrderAction,
	SET_RECENTLY_FINISHED_ORDER,
	UNSET_ORDERS,
} from '../actions/orders'
import { IOrders, IOrderSkeleton } from '../models/order'

export interface IOrdersState {
	deliveryWindow: null | IDeliveryWindow
	orderLoading: boolean
	ordersLoading: boolean
	items: IOrders
	recentlyFinishedOrder: null | IOrderSkeleton
}

const initialState: IOrdersState = {
	deliveryWindow: null,
	orderLoading: false,
	ordersLoading: false,
	items: {},
	recentlyFinishedOrder: null,
}

// tslint:disable:function-name
export default handleActions(
	{
		[SET_DELIVERY_WINDOW](state, { payload }: SetDeliveryWindowAction) {
			return {
				...state,
				deliveryWindow: payload,
			}
		},
		[SET_ORDER_LOADING](state) {
			return {
				...state,
				orderLoading: true,
			}
		},
		[UNSET_ORDER_LOADING](state) {
			return {
				...state,
				orderLoading: false,
			}
		},
		[SET_ORDERS](state, { payload } : SetOrdersAction) {
			return {
				...state,
				ordersLoading: false,
				items: { ...payload },
			}
		},
		[UNSET_RECENTLY_FINISHED_ORDER](state) {
			return {
				...state,
				recentlyFinishedOrder: null,
			}
		},
		[SET_RECENTLY_FINISHED_ORDER](state, { payload } : SetRecentlyFinishedOrderAction) {
			return {
				...state,
				recentlyFinishedOrder: { ...payload },
			}
		},
		[UNSET_ORDERS](state) {
			return {
				...state,
				recentlyFinishedOrder: null,
				items: {},
			}
		},
	},
	initialState,
)



