import React from 'react'
import styles from './styles.sass'
import Popup from '../Popup'
import { IPaymentCard } from '../../models/payment'
import translate from '../../utils/translate'

export interface IPaymentsOwnProps {
	isActive: boolean
	onClose: () => void
}

export interface IPaymentsProps {
	card: IPaymentCard
}

export interface IPaymentsActionProps {
	removeCard: () => void
}

export interface IProps extends IPaymentsOwnProps, IPaymentsProps, IPaymentsActionProps {}

export default class Payments extends React.Component<
	IProps
> {

	componentWillReceiveProps(nextProps: IProps) {
		if (this.props.card && !nextProps.card) {
			this.props.onClose()
		}
	}

	render() {
		const { card, isActive, onClose } = this.props

		return (
			<Popup
				isActive={isActive}
				onClose={onClose}
				title={translate('cards.title')}
			>
				<section className={styles.wrapper}>
					{card && (
						<div className={styles.item}>
							<div className={styles.card}>
								<div className={styles.icon}>
									{this.getCardIcon(card)}
								</div>
								<div className={styles.number}>
									{card.number}
								</div>
							</div>
							<div className={styles.remove}>
								<button
									role="button"
									onClick={this.removeCard}
									aria-label="Remove card"
									className={styles.removeButton}
								>
									<i className="fas fa-trash"></i>
								</button>
							</div>
						</div>
					)}
				</section>
			</Popup>
		)
	}

	private removeCard = () => {
		const { card, removeCard } = this.props
		if (confirm(translate('payment.cardDeleteConfirm.text').replace('%1$s', card.number))) {
			removeCard()
		}
	}

	private getCardIcon(card: IPaymentCard) {
		let className: string

		switch (card.issuer) {
		case 'visa':
			className = 'fab fa-cc-visa'
			break
		case 'mastercard':
			className = 'fab fa-cc-mastercard'
			break
		default:
			className = 'fas fa-credit-card'
		}

		return <i className={className}></i>
	}
}



