import { connect } from 'react-redux'
import { loginWithEmailPassword } from '../actions/user'
import { IState } from '../reducers'
import Login, { ILoginActionProps } from '../components/Login'

const mapStateToProps = (state: IState) => ({ loading: state.user.loading })
const mapDispatchToProps: ILoginActionProps = { loginWithEmailPassword }

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(Login)



