import React from 'react'
import Products from '../Products'
import { IPromotions } from '../../actions/promotions'

export interface IPromotionsProps {
	promotions: IPromotions
	loading: boolean
}

export interface IPromotionsActionProps {
	requestPromotions: () => void
}

export default class Offer extends React.Component<
	IPromotionsProps & IPromotionsActionProps
> {
	componentWillMount() {
		this.props.requestPromotions()
	}

	render() {
		const { loading, promotions } = this.props
		let promotion = null

		if (Object.keys(promotions).length > 0) {
			promotion = Object.values(promotions)[0] // Only the first promotion gets used
		}

		return (
			<Products
				shortLabels
				products={loading ? undefined : promotion ? promotion.products : {}}
			/>
		)
	}
}



