import { connect } from 'react-redux'
import { IState } from '../reducers'
import App from '../components/App'

const mapStateToProps = (state: IState) => ({ loggedIn: state.user.loggedIn })
const mapDispatchToProps = {}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(App)



