import { connect } from 'react-redux'
import { IState } from '../reducers'
import OrderRecapitulation, {
	IOrderRecapitulationActionProps,
	IOrderRecapitulationOwnProps,
} from '../components/OrderRecapitulation'
import { requestAddresses } from '../actions/addresses'
import { setDefaultAddress, setDefaultDate } from '../actions/user'
import { requestDeliveryWindow, placeOrder } from '../actions/orders'

const mapStateToProps = (state: IState, ownProps: IOrderRecapitulationOwnProps) => ({
	...ownProps,
	addresses: state.addresses.items,
	items: state.basket,
	selectedAddress: state.user.defaultAddress,
	selectedDate: state.user.defaultDate,
	deliveryWindow: state.orders.deliveryWindow,
	orderLoading: state.orders.orderLoading,
})
const mapDispatchToProps: IOrderRecapitulationActionProps = {
	requestAddresses,
	setDefaultAddress,
	setDefaultDate,
	requestDeliveryWindow,
	placeOrder,
}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(OrderRecapitulation)



