import initialTranslation from '../initialTranslation.json'

interface IText {
	[language: string]: {
		other: string
	}
}

class Translator {
	private activeLanguage: string = initialTranslation.default
	private texts: {
		[key: string]: IText
	} = initialTranslation.data

	public translate = (key: string) => {
		if (
			this.texts[key] &&
			typeof this.texts[key][this.activeLanguage] !== undefined &&
			typeof this.texts[key][this.activeLanguage].other !== undefined
		) {
			return this.texts[key][this.activeLanguage].other
		}
		return key
	}
}

export default new Translator().translate



