import handleActions from '../utils/handleActions'
import { SET_PROMOTIONS, SetPromotionsAction, UNSET_PROMOTIONS } from '../actions/promotions'
import { IProducts } from '../actions/products'

export interface IPromotion {
	id: string
	name: string
	intro: string
	products: IProducts
}

export interface IPromotionsState {
	loading: boolean
	items: { [id: string]: IPromotion }
}

const initialState: IPromotionsState = {
	loading: true,
	items: {},
}

// tslint:disable:function-name
export default handleActions(
	{
		[SET_PROMOTIONS](state, { payload }: SetPromotionsAction) {
			return {
				...state,
				loading: false,
				items: { ...payload },
			}
		},
		[UNSET_PROMOTIONS](state) {
			return {
				...state,
				loading: true,
				items: {},
			}
		},
	},
	initialState,
)



