function capitalizeFirstLetter(input: string) {
	return input.charAt(0).toUpperCase() + input.slice(1)
}

export default function formatIngredients(ingredients: string[]) {
	return capitalizeFirstLetter(ingredients.join(', ').toLocaleLowerCase())
}



