import { connect } from 'react-redux'
import { requestAddresses, removeAddress } from '../actions/addresses'
import { setDefaultAddress } from '../actions/user'
import { IState } from '../reducers'
import Addresses, {
	IAddressesActionProps,
} from '../components/Addresses'

const mapStateToProps = (state: IState) => ({
	addresses: state.addresses.items,
	defaultAddress: state.user.defaultAddress,
})
const mapDispatchToProps: IAddressesActionProps = {
	requestAddresses,
	removeAddress,
	setDefaultAddress,
}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(Addresses)



