import { combineReducers } from 'redux'
import user, { IUserState } from './user'
import addresses, { IAddressesState } from './addresses'
import basket, { IBasketState } from './basket'
import products, { IProductsState } from './products'
import promotions, { IPromotionsState } from './promotions'
import notifications, { INotificationsState } from './notifications'
import orders, { IOrdersState } from './orders'
import general, { IGeneralState } from './general'

export interface IState {
	addresses: IAddressesState
	basket: IBasketState
	user: IUserState
	products: IProductsState
	promotions: IPromotionsState
	notifications: INotificationsState
	orders: IOrdersState
	general: IGeneralState
}

export default combineReducers({
	addresses,
	basket,
	user,
	products,
	promotions,
	notifications,
	orders,
	general,
})



