import React from 'react'
import styles from './styles.sass'
import translate from '../../utils/translate'
import { IAddress } from '../../reducers/addresses'
import Addresses from '../../containers/AddressesContainer'
import Payments from '../../containers/PaymentsContainer'
import Orders from '../../containers/OrdersContainer'
import { IPaymentCard } from '../../models/payment'
import getVersion from '../../utils/getVersion'
import setAnalyticsView from '../../utils/setAnalyticsView'
import { AnalyticsView } from '../../models/analytics'
//import ReactGA from 'react-ga'

export interface ISettingsProps {
	defaultAddress: IAddress | null
	card: IPaymentCard | null
}

export interface ISettingsActionProps {
	logoutAndPreventAutoSignIn: () => void
	requestUser: () => void
}

enum SettingsListItem {
	Addresses = 'Addresses',
	Payments = 'Payment',
	Repeating = 'RecurringOrders',
}

interface IState {
	activeListItem: SettingsListItem | null
}

export default class Settings extends React.Component<
	ISettingsProps & ISettingsActionProps, IState
> {
	private version = getVersion()

	state: IState = {
		activeListItem: null,
	}

	componentDidMount() {
		this.props.requestUser()
	}

	render() {
		const {
			logoutAndPreventAutoSignIn,
			defaultAddress,
			card,
	} = this.props
		const { activeListItem } = this.state

		const listItems = [
			{
				type: SettingsListItem.Addresses,
				title: translate('menu.myAddresses.title'),
				note: defaultAddress ? defaultAddress.title : translate('menu.myAddresses.desc'),
			},
		]
		if (card) {
			listItems.push({
				type: SettingsListItem.Payments,
				title: translate('menu.payments.title'),
				note: translate('menu.payments.desc'),
			})
		}
		// @TODO: reenable
		/*listItems.push({
			type: SettingsListItem.Repeating,
			title: translate('menu.repeating.title'),
			note: translate('menu.repeating.desc'),
		})*/

		return (
			<section className={styles.wrapper}>
				<div className={styles.list}>
					{listItems.map((item) => (
						<div className={styles.listItem} key={item.type}>
							<div className={styles.listTitle}>
								{item.title}
							</div>
							<p className={styles.listNote}>
								{item.note}
							</p>
							<div className={styles.listArrow}>
								<i className="fas fa-chevron-right"></i>
							</div>
							<button
								role="button"
								aria-label="Open"
								className={styles.listAction}
								onClick={this.setActiveListItem(item.type)}
							/>
						</div>
					))}
				</div>

				<div className={styles.other}>
					<button className={styles.logout} role="button" onClick={() => logoutAndPreventAutoSignIn()}>
						{translate('menu.logout')}
					</button>
					{/*<ReactGA.OutboundLink
						className={styles.social}
						eventLabel="https://www.messenger.com/t/goodloky"
						to="https://www.messenger.com/t/goodloky"
						target="_blank"
						rel="noopener"
						aria-label="Messenger"
					>
						<i className="fab fa-facebook-messenger"></i>
					</ReactGA.OutboundLink>
					<ReactGA.OutboundLink
						className={styles.social}
						eventLabel="https://www.instagram.com/goodlokcz/"
						to="https://www.instagram.com/goodlokcz/"
						target="_blank"
						rel="noopener"
						aria-label="Instagram"
					>
						<i className="fab fa-instagram"></i>
					</ReactGA.OutboundLink>*/}
				</div>

				<div className={styles.popups}>
					<Addresses
						isActive={activeListItem === SettingsListItem.Addresses}
						onClose={this.closeListItem}
					/>
					<Payments
						isActive={activeListItem === SettingsListItem.Payments}
						onClose={this.closeListItem}
					/>
					<Orders
						isActive={activeListItem === SettingsListItem.Repeating}
						onClose={this.closeListItem}
					/>
				</div>
				<div className={styles.version}>
					<span>
						Version: {this.version}
					</span>
				</div>
			</section>
		)
	}

	private closeListItem = () => {
		this.setState({ activeListItem: null })
		setAnalyticsView(AnalyticsView.Settings)
	}

	private setActiveListItem = (type: SettingsListItem) => () => {
		this.setState({ activeListItem: type })
		setAnalyticsView(AnalyticsView[type])
	}
}



