import createActionCreator, {
	IStandardAction,
} from '../utils/createActionCreator'
import { Dispatch } from 'redux'
import { IBasketItem } from '../reducers/basket'
import { IProduct } from '../reducers/products'
import { AnalyticsEventCategory, AnalyticsEventAction } from '../models/analytics'
import submitAnalyticsEvent from '../utils/submitAnalyticsEvent'

export interface IBasketItems {
	[id: string]: IBasketItem
}

export const ADD_BASKET_ITEM = 'ADD_BASKET_ITEM'
export type AddBasketItemAction = IStandardAction<IProduct>
const add = createActionCreator(ADD_BASKET_ITEM, (product: IProduct) => product)
export const addToBasket = (product: IProduct) => (dispatch: Dispatch<any>) => {
	submitAnalyticsEvent(AnalyticsEventCategory.Basket, AnalyticsEventAction.AddItem, product.id)
	dispatch(add(product))
}

export const REMOVE_BASKET_ITEM = 'REMOVE_BASKET_ITEM'
export type RemoveBasketItemAction = IStandardAction<IProduct>
const remove = createActionCreator(REMOVE_BASKET_ITEM, (product: IProduct) => product)
export const removeFromBasket = (product: IProduct) => (dispatch: Dispatch<any>) => {
	submitAnalyticsEvent(AnalyticsEventCategory.Basket, AnalyticsEventAction.RemoveItem, product.id)
	dispatch(remove(product))
}

export const EMPTY_BASKET = 'EMPTY_BASKET'
const empty = createActionCreator(EMPTY_BASKET, () => {})
export const emptyBasket = () => (dispatch: Dispatch<any>) => {
	dispatch(empty({}))
}



