import React from 'react'
import translate from '../../utils/translate'
import { IAddress } from '../../reducers/addresses'
import styles from './styles.sass'
import Loading from '../Loading'

export interface IAddressEditorOwnProps {
	address?: IAddress
	onDone?: () => void
}

export interface IAddressEditorProps {
	loading: boolean
}

export interface IAddressEditorActionProps {
	addAddress: (title: string, address: string) => void
	editAddress: (address: IAddress) => void
}

interface IProps extends IAddressEditorOwnProps, IAddressEditorProps, IAddressEditorActionProps {}

interface IState {
	title: string
	text: string
}

export default class AddressEditor extends React.Component<
	IProps,
	IState
> {
	private pendingChanges: boolean = false

	state: IState = {
		title: '',
		text: '',
	}

	componentWillMount() {
		const { address } = this.props

		if (address) {
			this.setState({
				title: address.title,
				text: address.address,
			})
		}
	}

	componentWillReceiveProps(nextProps: IProps) {
		if (nextProps.address && (!this.props.address || nextProps.address.id !== this.props.address.id)) {
			this.setState({
				title: nextProps.address.title,
				text: nextProps.address.address,
			})
		}

		if (this.pendingChanges && nextProps.onDone && this.props.loading && !nextProps.loading) {
			nextProps.onDone()
			this.pendingChanges = false
		}
	}

	render() {
		const { title, text } = this.state
		const { loading } = this.props

		return (
			<div className={styles.wrapper}>
				<form onSubmit={this.onSubmit.bind(this)}>
					<label>
						{translate('addresses.addressname')}:
						<br />
						<input
							className={styles.title}
							type="text"
							name="label"
							value={title}
							onChange={this.onTitleChange.bind(this)}
							placeholder={translate('addresses.addressname.placeholder')}
						/>
					</label>
					<br />
					<br />
					<label>
						{translate('addresses.addressloc')}:
						<br />
						<textarea
							className={styles.address}
							value={text}
							name="address"
							onChange={this.onAddressChange.bind(this)}
							placeholder={translate('addresses.addressloc.placeholder')}
						/>
					</label>
					<div className={styles.submit}>
						<button
							type="submit"
							disabled={this.isDisabled()}
						>
							{translate('addresses.save')} <i className="fas fa-arrow-right"></i>
						</button>
					</div>
				</form>
				<div className={styles.loading}>
					<Loading isActive={loading} />
				</div>
			</div>
		)
	}

	private isDisabled(): boolean {
		const { title, text } = this.state
		return !title || !text
	}

	private onTitleChange(event: React.FormEvent<HTMLInputElement>) {
		this.setState({ title: event.currentTarget.value })
	}

	private onAddressChange(event: React.FormEvent<HTMLInputElement>) {
		this.setState({ text: event.currentTarget.value })
	}

	private onSubmit(event: React.FormEvent<HTMLFormElement>) {
		event.preventDefault()

		if (this.isDisabled()) {
			return
		}

		const { title, text } = this.state
		const { addAddress, editAddress, address } = this.props

		if (address) {
			editAddress({
				...address,
				title,
				address: text,
			})
		} else {
			addAddress(title, text)
		}

		this.pendingChanges = true
	}
}



