import React from 'react'
import Login from '../../containers/LoginContainer'
import Registration from '../../containers/RegistrationContainer'
import translate from '../../utils/translate'
import styles from './styles.sass'
import { AnalyticsView } from '../../models/analytics'
import setAnalyticsView from '../../utils/setAnalyticsView'

enum View {
	Login = 'Login',
	Registration = 'Registration',
}

export interface IAnonymouseActionProps {
	autoLoginWithEmailPassword: (email: string, password: string) => void
}

export interface IState {
	view: View
}

export default class Anonymous extends React.Component<
	IAnonymouseActionProps, IState
> {
	state: IState = {
		view: View.Login,
	}

	componentDidMount() {
		setAnalyticsView(AnalyticsView[this.state.view])
		this.tryAutoSignIn()
	}

	render() {
		const { view } = this.state

		return (
			<div className={styles.wrapper}>
				{view === View.Login ? <Login /> : <Registration />}
				<div className={styles.switch}>
					{view === View.Login && translate('login.noAccount')}
					{view === View.Registration && translate('login.haveAccount')}
					{' '}
					<button
						role="button"
						onClick={this.switchView}
						className={styles.switchAction}
					>
						{view === View.Login && translate('login.register')}
						{view === View.Registration && translate('login.login')}
					</button>
				</div>
			</div>
		)
	}

	private switchView = () => {
		const { view } = this.state

		if (view === View.Login) {
			this.setState({ view: View.Registration })
			setAnalyticsView(AnalyticsView[View.Registration])
		} else if (view === View.Registration) {
			this.setState({ view: View.Login })
			setAnalyticsView(AnalyticsView[View.Login])
		}

	}

	private tryAutoSignIn() {
		const { autoLoginWithEmailPassword } = this.props

		// @ts-ignore
		if (window.PasswordCredential) {
			// @ts-ignore
			navigator.credentials.get({
				password: true,
				mediation: 'silent',
			}).then((c: any) => {
				if (c) {
					return {
						email: c.id,
						password: c.password,
					}
				}
			}).then((profile: any) => {
				if (profile) {
					autoLoginWithEmailPassword(profile.email, profile.password)
				}
			}).catch((error: any) => {
				console.error(error)
				console.info('Sign-in Failed')
			})
		}
	}
}



