import { connect } from 'react-redux'
import { logoutAndPreventAutoSignIn, requestUser } from '../actions/user'
import Settings, {
	ISettingsActionProps,
} from '../components/Settings'
import { IState } from '../reducers'

const mapStateToProps = (state: IState) => ({
	defaultAddress: state.user.defaultAddress,
	card: state.user.card,
})
const mapDispatchToProps: ISettingsActionProps = {
	logoutAndPreventAutoSignIn,
	requestUser,
}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(Settings)



