import React from 'react'
import { INotification, NotificationSeverity } from '../../reducers/notifications'
import styles from './styles.sass'
import cn from 'classnames'
import { INotifications } from '../../actions/notifications'

const NOTIFICATION_LIFESPAN = 7000

interface ILocalNotification {
	dismissed: boolean
	notification: INotification
}

interface ILocalNotifications {
	[id: string]: ILocalNotification
}

export interface INotificationsProps {
	notifications: INotifications
}

export interface INotificationsActionProps {
	dismissNotification: (notification: INotification) => void
}

interface IState {
	notifications: ILocalNotifications
}

interface IProps extends INotificationsProps, INotificationsActionProps {}

export default class Notifications extends React.Component<
	IProps, IState
> {
	private mounted: boolean = false

	state: IState = {
		notifications: {},
	}

	componentDidMount() {
		this.mounted = true
	}

	componentWillUnmount() {
		this.mounted = false
	}

	componentWillReceiveProps(nextProps: IProps) {
		// @TODO: too naive approach - dismissed notifications kept in memory/rerendering
		const newLocalNotifications = this.state.notifications
		Object.values(nextProps.notifications).forEach((notification) => {
			if (!this.props.notifications[notification.id]) {
				newLocalNotifications[notification.id] = {
					notification,
					dismissed: false,
				}
				setTimeout(() => {
					if (this.mounted) {
						this.props.dismissNotification(notification)
					}
				}, NOTIFICATION_LIFESPAN)
			}
		})
		Object.values(this.props.notifications).forEach((notification) => {
			if (!nextProps.notifications[notification.id]) {
				newLocalNotifications[notification.id].dismissed = true
			}
		})
		this.setState({ notifications: newLocalNotifications })
	}

	render() {
		const { dismissNotification } = this.props
		const { notifications } = this.state

		return (
			<div className={styles.wrapper}>
				{Object.values(notifications).map((item) => (
					<div key={item.notification.id} className={cn(
						styles.item,
						item.notification.severity === NotificationSeverity.Info && styles.viewInfo,
						item.notification.severity === NotificationSeverity.Warning && styles.viewWarning,
						item.notification.severity === NotificationSeverity.Danger && styles.viewDanger,
						item.dismissed && styles.isDismissed,
					)}>
						<div className={styles.content} onClick={() => dismissNotification(item.notification)}>
							{item.notification.title}
						</div>
						<button
							role="button"
							onClick={() => dismissNotification(item.notification)}
							className={styles.dismiss}
							aria-label="Dismiss notification"
						>
							<i className="fas fa-times"></i>
						</button>
					</div>
				))}
			</div>
		)
	}
}



