import React from 'react'
import styles from './styles.sass'
import cn from 'classnames'
import translate from '../../utils/translate'

export enum TopNavigationSection {
	Now = 'Promotion',
	All = 'AllProducts',
	Settings = 'Settings',
}

interface ITopNavigationProps {
	active: TopNavigationSection
	onChange: (type: TopNavigationSection) => void
}

export default class TopNavigation extends React.Component<
	ITopNavigationProps
> {
	static sections = [
		{
			type: TopNavigationSection.Now,
			label: translate('navbar.now'),
		},
		{
			type: TopNavigationSection.All,
			label: translate('navbar.all'),
		},
		{
			type: TopNavigationSection.Settings,
			label: <i className="fas fa-user" />,
		},
	]

	render() {
		const { active } = this.props

		return (
			<nav className={styles.wrapper}>
				{TopNavigation.sections.map((section) => (
					<button
						role="button"
						className={cn(
							styles.section,
							active === section.type && styles.isActive,
						)}
						onClick={this.onChange(section.type)}
						key={section.type}
					>
						{section.label}
					</button>
				))}
				<div className={styles.bar} />
			</nav>
		)
	}

	private onChange = (type: TopNavigationSection) => () => {
		this.props.onChange(type)
	}
}



