import React from 'react'
import styles from './styles.sass'
import SelectStrip, { ISelectStripItem } from '../SelectStrip'
import AddAddressModal from '../AddAddressModal'
import { IAddresses } from '../../actions/addresses'
import nl2br from '../../utils/nl2br'
import { IAddress } from '../../reducers/addresses'
import BasketTuner from '../../containers/BasketTunerContainer'
import { IBasketItems } from '../../actions/basket'
import Popup from '../Popup'
import { IDate } from '../../models/date'
import formatPrice from '../../utils/formatPrice'
import { IDeliveryWindow } from '../../actions/orders'
import Loading from '../Loading'
import { AnalyticsView } from '../../models/analytics'
import setAnalyticsView from '../../utils/setAnalyticsView'

const today = new Date()

const DEFAULT_DATES: IDate[] = [
	{
		id: 'default-1200',
		label: '12:00',
		value: new Date(today.getFullYear(), today.getMonth(), today.getDate(), 12).toISOString(),
	},
	{
		id: 'default-1400',
		label: '14:00',
		value: new Date(today.getFullYear(), today.getMonth(), today.getDate(), 14).toISOString(),
	},
	{
		id: 'default-tomorrow-1000',
		label: 'Tomorrow 10:00',
		value: new Date(today.getFullYear(), today.getMonth(), today.getDate() + 1, 10).toISOString(),
	},
]

export interface IOrderRecapitulationOwnProps {
	isActive: boolean
	onClose: () => void
}
export interface IOrderRecapitulationProps {
	addresses: IAddresses
	items: IBasketItems
	selectedAddress: IAddress | null
	selectedDate: IDate | null
	deliveryWindow: IDeliveryWindow | null
	orderLoading: boolean
}

export interface IOrderRecapitulationActionProps {
	requestAddresses: () => void
	setDefaultAddress: (address: IAddress) => void
	setDefaultDate: (time: IDate) => void
	requestDeliveryWindow: () => void
	placeOrder: (items: IBasketItems, address: IAddress, date: IDate) => void
}

interface IState {
	addAddress: boolean
}

interface IProps extends IOrderRecapitulationOwnProps, IOrderRecapitulationProps, IOrderRecapitulationActionProps {}

export default class OrderRecapitulation extends React.Component<
	IProps, IState
> {
	state: IState = {
		addAddress: false,
	}

	componentDidMount() {
		this.props.requestAddresses()
		this.props.requestDeliveryWindow()
	}

	componentWillReceiveProps(nextProps: IProps) {
		const itemsCountBefore = Object.keys(this.props.items).length
		const itemsCountAfter = Object.keys(nextProps.items).length
		if (itemsCountBefore !== itemsCountAfter && itemsCountAfter === 0) {
			nextProps.onClose()
		}
	}

	render() {
		const {
			isActive,
			onClose,
			selectedAddress,
			selectedDate,
			orderLoading,
		} = this.props
		const { addAddress } = this.state

		const orderDisabled = !selectedAddress || !selectedDate || orderLoading

		return (
			<Popup
				isActive={isActive}
				onClose={onClose}
				bottomBar={(
					<React.Fragment>
						<div>
							Price: <span className={styles.orderPriceValue}>{this.getPrice()}</span>
						</div>
						<div>
							Order <i className="fas fa-arrow-right"></i>
						</div>
						<button
							role="button"
							onClick={this.order}
							aria-label="Order"
							className={styles.orderAction}
							disabled={orderDisabled}
						/>
					</React.Fragment>
				)}
			>
				<div className={styles.wrapper}>
					<div className={styles.products}>
						<BasketTuner />
					</div>
					<div className={styles.deliveryDetails}>
						<div className={styles.deliveryDetailsTitle}>Where?</div>
						<SelectStrip items={this.getWhereItems()} />
						<div className={styles.deliveryDetailsDivider} />
						<div className={styles.deliveryDetailsTitle}>When?</div>
						<SelectStrip items={this.getWhenItems()} />
					</div>
					<div className={styles.loading}>
						<Loading isActive={orderLoading} />
					</div>
				</div>
				<AddAddressModal
					isActive={addAddress}
					onClose={this.hideAddAddress}
				/>
			</Popup>
		)
	}

	private getPrice(): string {
		return formatPrice(Object.values(this.props.items).reduce((total, current) => {
			return total + current.quantity * current.product.price
		}, 0))
	}

	private order = () => {
		const { selectedAddress, selectedDate, items, placeOrder } = this.props
		placeOrder(items, selectedAddress, selectedDate)
	}

	private getWhereItems(): ISelectStripItem[] {
		const { setDefaultAddress, selectedAddress } = this.props

		const addItem: ISelectStripItem = {
			active: false,
			note: <React.Fragment><i className="fas fa-plus"></i> add address</React.Fragment>,
			onClick: this.showAddAddress,
		}
		return Object.values(this.props.addresses).map((address): ISelectStripItem => ({
			active: selectedAddress && selectedAddress.id === address.id,
			title: address.title,
			note: nl2br(address.address),
			onClick: () => setDefaultAddress(address),
		})).concat(addItem)
	}

	private getWhenItems(): ISelectStripItem[] {
		const { setDefaultDate, selectedDate, deliveryWindow } = this.props

		const customTime: ISelectStripItem = {
			active: false,
			title: <React.Fragment><i className="far fa-edit"></i> custom time</React.Fragment>,
			onClick: () => console.log('@TODO: Custom time'),
		}

		const dates: IDate[] = [
			...DEFAULT_DATES,
		]

		if (deliveryWindow) {
			dates.unshift(deliveryWindow.date)
		}

		return dates.map((date): ISelectStripItem => ({
			active: selectedDate && date.id === selectedDate.id,
			title: date.label,
			onClick: () => setDefaultDate(date),
		})).concat(customTime)
	}

	private showAddAddress = () => {
		this.setState({ addAddress: true })
		setAnalyticsView(AnalyticsView.AddAddress)
	}

	private hideAddAddress = () => {
		this.setState({ addAddress: false })
	}
}



