import React from 'react'
import styles from './styles.sass'
import cn from 'classnames'

export interface ISelectStripItem {
	active: boolean
	title?: string | JSX.Element
	note?: string | JSX.Element
	onClick: () => void
}

export interface ISelectStripProps {
	items: ISelectStripItem[]
}

export default class SelectStrip extends React.Component<
	ISelectStripProps
> {

	render() {
		const { items } = this.props

		return (
			<section className={styles.wrapper}>
				<div className={styles.in}>
					{items.map((item, i) => (
						<div key={i} className={cn(
							styles.item,
							item.active && styles.isActive,
							item.title && item.note && styles.viewRich,
						)}>
							<div className={styles.itemIn}>
								<button className={styles.action} role="button" onClick={item.onClick} aria-label="Select"></button>
								{item.title && <div className={styles.title}>{item.title}</div>}
								{item.note && <div className={styles.note}>{item.note}</div>}
							</div>
						</div>
					))}
				</div>
			</section>
		)
	}
}



