import React from 'react'
import { IProducts } from '../../actions/products'
import styles from './styles.sass'
import cn from 'classnames'
import smoothHorizontalScroll from '../../utils/smoothHorizontalScroll'

interface IProps {
	products: IProducts
	active: number
	onClick: (i: number) => void
	short?: boolean
}

export default class ProductLabels extends React.Component<
	IProps
> {
	private wrapper: HTMLDivElement
	private wrapperWidth: number = 1

	componentDidMount() {
		this.center(this.props.active)
		window.addEventListener('resize', this.recalculateSizes)
		this.recalculateSizes()
	}

	componentWillReceiveProps(nextProps: IProps) {
		if (nextProps.active !== this.props.active) {
			this.center(nextProps.active)
		}
	}

	componentWillUnmount() {
		window.removeEventListener('resize', this.recalculateSizes)
	}

	render() {
		const { products, active, onClick, short } = this.props

		return (
			<div className={styles.wrapper} ref={(el) => this.wrapper = el}>
				<div className={styles.items}>
					{Object.values(products).map((product, i) => (
						<div
							className={cn(
								styles.item,
								i === active && styles.isActive,
								short && styles.viewShort,
							)}
							key={product.id}
							style={{
								color: product.color,
							}}
						>
							<button
								role="button"
								onClick={() => onClick(i)}
								className={styles.in}
							>
								<span className={styles.text}>
									{short ? product.shortName : product.name}
								</span>
							</button>
						</div>
					))}
				</div>
			</div>
		)
	}

	private recalculateSizes = () => {
		this.wrapperWidth = this.wrapper.clientWidth
		this.center(this.props.active)
	}

	private center(active: number) {
		const targetButton: HTMLDivElement = this.wrapper.querySelector(`.${styles.item}:nth-child(${active + 1})`)
		if (!targetButton) {
			return
		}

		const targetScrollPosition = targetButton.offsetLeft + (targetButton.clientWidth - this.wrapperWidth) / 2

		smoothHorizontalScroll(this.wrapper, targetScrollPosition)
	}
}



