import { connect } from 'react-redux'
import { IState } from '../reducers'
import PasswordReset, { IPasswordResetActionProps } from '../components/PasswordReset'
import { addNotification } from '../actions/notifications'

const mapStateToProps = (_: IState) => ({})
const mapDispatchToProps: IPasswordResetActionProps = {
	addNotification,
}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(PasswordReset)



