import createActionCreator, {
	IStandardAction,
} from '../utils/createActionCreator'
import { Dispatch } from 'redux'
import { INotification } from '../reducers/notifications'

let lastId = 0

export interface INotifications {
	[id: string]: INotification
}

export const ADD_NOTIFICATION = 'ADD_NOTIFICATION'
export type AddNotificationAction = IStandardAction<INotification>
const add = createActionCreator(ADD_NOTIFICATION, (notification: INotification) => notification)
export const addNotification = (
	title: INotification['title'],
	severity: INotification['severity'],
) => (dispatch: Dispatch<any>): INotification => {
	lastId += 1
	const notification = {
		title,
		severity,
		id: `${lastId}`,
	}
	dispatch(add(notification))
	return notification
}

export const DISMISS_NOTIFICATION = 'DISMISS_NOTIFICATION'
export type DismissNotificationAction = IStandardAction<INotification>
const dismiss = createActionCreator(DISMISS_NOTIFICATION, (notification: INotification) => notification)
export const dismissNotification = (notification: INotification) => (dispatch: Dispatch<any>) => {
	dispatch(dismiss(notification))
}



