import React from 'react'
import styles from './styles.sass'
import { IProduct } from '../../reducers/products'
import cn from 'classnames'
import formatVolumePrice from '../../utils/formatVolumePrice'
import formatIngredients from '../../utils/formatIngredients'
import Bottle from '../Bottle'
import isProductSmall from '../../utils/isProductSmall'

export interface IProductProps {
	product?: IProduct
}

export interface IProductActionProps {
	addToBasket?: (product: IProduct) => void
}

interface IState {
	imageLoaded: boolean
	waitingForImageLoad: boolean
	flyingToBasket: number
}

export default class Product extends React.Component<
	IProductProps & IProductActionProps, IState
> {
	state: IState = {
		imageLoaded: false,
		waitingForImageLoad: true,
		flyingToBasket: 0,
	}

	componentDidMount() {
		setTimeout(() => {
			this.setState({ waitingForImageLoad: false })
		}, 20)
	}

	render() {
		const { product } = this.props
		const { imageLoaded, waitingForImageLoad, flyingToBasket } = this.state

		const name = product ? product.name : ''
		const price = product ? formatVolumePrice(product.volume, product.price) : ''
		const ingredients = product ? formatIngredients(product.ingredients) : ''
		const color = product && product.color

		return (
			<section
				className={cn(
					styles.wrapper,
					!product && styles.viewShadow,
				)}
				style={{ color }}
			>
				<div className={styles.in}>
					<div className={styles.image}>
						{product && (
							<button
								className={styles.addToCart}
								role="button"
								aria-label="Add to basket"
								onClick={this.add}
							/>
						)}
						<div
							className={cn(
								styles.imageFrame,
								!waitingForImageLoad && !imageLoaded && styles.isPlaceholderVisible,
								imageLoaded && styles.isRealVisible,
								product && isProductSmall(product) && styles.viewSmall,
							)}
						>
							<Bottle className={cn(styles.imageIn, styles.viewPlaceholder)} />
							{Array(flyingToBasket).fill(0).map((_, i) => (
								<Bottle
									key={i}
									className={cn(
										styles.imageIn,
										styles.viewPlaceholder,
										styles.viewFlying,
									)} />
							))}
							{product && (
								<img
									className={cn(styles.imageIn, styles.viewReal)}
									src={product.image}
									onLoad={this.onImageLoaded.bind(this)}
								/>
							)}
						</div>
					</div>
					<div className={styles.info}>
						<div className={styles.infoIn}>
							<h1 className={styles.name}>{name}</h1>
							<p className={styles.price}>{price}</p>
							<p className={styles.note}>{ingredients}</p>
						</div>
					</div>
				</div>
			</section>
		)
	}

	private add = () => {
		const { product, addToBasket } = this.props
		const { flyingToBasket } = this.state
		this.setState({
			flyingToBasket: flyingToBasket + 1,
		})
		addToBasket(product)
	}

	private onImageLoaded() {
		this.setState({ imageLoaded: true })
	}
}



