import createActionCreator, {
	IStandardAction,
} from '../utils/createActionCreator'
import { Dispatch } from 'redux'
import { IProduct } from '../reducers/products'
import api from '../utils/api'

export interface IProducts {
	[id: string]: IProduct
}

export const SET_PRODUCTS = 'SET_PRODUCTS'
export type SetProductsAction = IStandardAction<IProducts>
const setProducts = createActionCreator(
	SET_PRODUCTS,
	(products: IProducts) => products,
)
export const requestProducts = () => (dispatch: Dispatch<any>) => {
	api.requestProducts().then((data) => {
		dispatch(setProducts(data))
	})
}

export const UNSET_PRODUCTS = 'UNSET_PRODUCTS'
const unset = createActionCreator(UNSET_PRODUCTS, () => {})
export const unsetProducts = () => (dispatch: Dispatch<any>) => {
	dispatch(unset({}))
}



