import { connect } from 'react-redux'
import Payments, { IPaymentsActionProps } from '../components/Payments'
import { IState } from '../reducers'
import { removeCard } from '../actions/user'

const mapStateToProps = (state: IState) => ({
	card: state.user.card,
})
const mapDispatchToProps: IPaymentsActionProps = {
	removeCard,
}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(Payments)



