import React from 'react'
import translate from '../../utils/translate'
import api from '../../utils/api'
import { NotificationSeverity } from '../../reducers/notifications'
import Popup from '../Popup'
import styles from './styles.sass'
import Loading from '../Loading'
import submitAnalyticsEvent from '../../utils/submitAnalyticsEvent'
import { AnalyticsEventCategory, AnalyticsEventAction } from '../../models/analytics'

export interface IPasswordResetOwnProps {
	prefilledEmail: string
	isActive: boolean
	onClose: () => void
}

export interface IPasswordResetActionProps {
	addNotification: (title: string, severity: NotificationSeverity) => void
}

interface IState {
	email: string
	loading: boolean
}

interface IProps extends IPasswordResetOwnProps, IPasswordResetActionProps {}

export default class PasswordReset extends React.Component<
	IProps,
	IState
> {
	state: IState = {
		email: this.props.prefilledEmail,
		loading: false,
	}

	componentWillReceiveProps(nextProps: IProps) {
		if (nextProps.prefilledEmail !== this.props.prefilledEmail) {
			this.setState({ email: nextProps.prefilledEmail })
		}
	}

	render() {
		const { email, loading } = this.state
		const { isActive, onClose } = this.props

		return (
			<Popup
				isActive={isActive}
				onClose={onClose}
				title={translate('login.forgottenPwd')}
			>
				<form className={styles.wrapper} onSubmit={this.onSubmit.bind(this)}>
					<label>
						{translate('forgottenPwd.yourEmail')}
						<br />
						<input
							type="email"
							name="email"
							autoComplete="email"
							value={email}
							disabled={loading}
							onChange={this.onEmailChange.bind(this)}
						/>
					</label>
					<br />
					<br />
					<br />
					<button type="submit" disabled={this.isSubmitDisabled()}>
						{translate('forgottenPwd.resetPwd')}
						<i className="fas fa-arrow-right"></i>
					</button>
					<div className={styles.loading}>
						<Loading isActive={loading} />
					</div>
				</form>
			</Popup>
		)
	}

	private isSubmitDisabled() {
		return this.state.loading || !this.state.email
	}

	private onEmailChange(event: React.FormEvent<HTMLInputElement>) {
		this.setState({ email: event.currentTarget.value })
	}

	private onSubmit(event: React.FormEvent<HTMLFormElement>) {
		event.preventDefault()

		const { email } = this.state

		if (!this.isSubmitDisabled()) {
			this.setState({ loading: true })
			submitAnalyticsEvent(AnalyticsEventCategory.User, AnalyticsEventAction.ResetPassword)
			api.passwordReset(email)
				.then(() => {
					this.props.addNotification(
						translate('forgottenPwd.weSentEmail'),
						NotificationSeverity.Info,
					)
					this.setState({
						email: '',
					})
					this.props.onClose()
				})
				.catch(() => {})
				.then(() => {
					this.setState({ loading: false })
				})
		}
	}
}



