import React from 'react'
import { IProducts } from '../../actions/products'
import styles from './styles.sass'
import Product from '../../containers/ProductContainer'
import ProductLabels from '../ProductLabels'
import { IProduct } from '../../reducers/products'
import smoothHorizontalScroll from '../../utils/smoothHorizontalScroll'

const SCROLL_DEBOUNCE_DURATION = 150

export interface IProductsProps {
	products?: IProducts
	shortLabels?: boolean
}

interface IState {
	active: number
}

export default class Products extends React.Component<
	IProductsProps, IState
> {
	private counter: HTMLDivElement
	private counterScrollTimer: number
	private productWidth: number = 1
	private wrapperWidth: number = 1
	private initialOffset: number = 0

	state: IState = {
		active: 0,
	}

	componentDidMount() {
		this.counter.addEventListener('scroll', this.onCounterScroll)
		window.addEventListener('resize', this.onResize)
		this.recalculateSizes()
	}

	componentWillUnmount() {
		this.counter.removeEventListener('scroll', this.onCounterScroll)
		window.removeEventListener('resize', this.onResize)
	}

	render() {
		const { products, shortLabels } = this.props
		const { active } = this.state

		return (
			<div className={styles.wrapper}>
				<div className={styles.background} />
				<div className={styles.counter} ref={(el) => this.counter = el}>
					<div className={styles.counterIn}>
						{products
							? Object.values(products).map((product, i) => this.renderProduct(i, product))
							: [1, 2, 3, 4, 5].map((_, i) => this.renderProduct(i))}
					</div>
				</div>
				<div className={styles.labels}>
					{products && (
						<ProductLabels
							products={products}
							active={active}
							onClick={this.setActive}
							short={shortLabels}
						/>
					)}
				</div>
			</div>
		)
	}

	private renderProduct(key: number, product?: IProduct) {
		return (
			<div className={styles.product} key={key}>
				<Product product={product} />
			</div>
		)
	}

	private setActive = (index: number) => {
		this.setState({ active: index })
		this.scrollTo(index)
	}

	private scrollTo(index: number) {
		const targetProduct = this.counter.querySelector(`.${styles.product}:nth-child(${index + 1})`)
		if (!targetProduct) {
			return
		}

		const targetScrollPosition = index * this.productWidth
		smoothHorizontalScroll(this.counter, targetScrollPosition)
	}

	private onResize = () => {
		this.recalculateSizes()
		this.scrollTo(this.state.active)
	}

	private recalculateSizes = () => {
		const firstProduct = this.counter.querySelector(`.${styles.product}`)
		if (firstProduct) {
			this.productWidth = firstProduct.clientWidth // Assumption: all products have the same width
		}

		this.wrapperWidth = this.counter.clientWidth
		this.initialOffset = (this.wrapperWidth - this.productWidth) / 2
	}

	private onCounterScroll = () => {
		window.clearTimeout(this.counterScrollTimer)
		this.counterScrollTimer = window.setTimeout(this.onCounterScrollStopped, SCROLL_DEBOUNCE_DURATION)
	}

	private onCounterScrollStopped = () => {
		const scrollCenterPosition = this.counter.scrollLeft + this.wrapperWidth / 2
		this.setActive(Math.floor((scrollCenterPosition - this.initialOffset) / this.productWidth))
	}
}



