import React from 'react'
import translate from '../../utils/translate'
import logo from '../Login/logo.svg'
import styles from '../Login/styles.sass'
import Loading from '../Loading'
import { AnalyticsEventCategory, AnalyticsEventAction } from '../../models/analytics'
import submitAnalyticsEvent from '../../utils/submitAnalyticsEvent'

export interface IRegistrationProps {
	loading: boolean
}

export interface IRegistrationActionProps {
	registerWithEmailPassword: (email: string, password: string) => void
}

export interface IRegistrationState {
	email: string
	password: string
}

export default class Registration extends React.Component<
	IRegistrationProps & IRegistrationActionProps,
	IRegistrationState
> {
	state: IRegistrationState = {
		email: '',
		password: '',
	}

	render() {
		const {
			email,
			password,
		} = this.state
		const { loading } = this.props

		return (
			<div className={styles.wrapper}>
				<img src={logo} width="94" height="34" alt="Logo" />
				<form onSubmit={this.onSubmit.bind(this)}>
					<label>
						{translate('login.regEmail')}
						<br />
						<input
							type="email"
							name="email"
							autoComplete="email"
							value={email}
							disabled={loading}
							onChange={this.onEmailChange.bind(this)}
						/>
					</label>
					<br />
					<br />
					<br />
					<label>
						{translate('login.loginPassword')}
						<br />
						<input
							type="password"
							name="password"
							autoComplete="current-password"
							value={password}
							disabled={loading}
							onChange={this.onPasswordChange.bind(this)}
						/>
					</label>
					<br />
					<br />
					<br />
					<button type="submit" disabled={loading}>
						{translate('login.doRegister')}
						<i className="fas fa-arrow-right"></i>
					</button>
				</form>
				<div className={styles.loading}>
					<Loading isActive={loading} />
				</div>
			</div>
		)
	}

	private onEmailChange(event: React.FormEvent<HTMLInputElement>) {
		this.setState({ email: event.currentTarget.value })
	}

	private onPasswordChange(event: React.FormEvent<HTMLInputElement>) {
		this.setState({ password: event.currentTarget.value })
	}

	private onSubmit(event: React.FormEvent<HTMLFormElement>) {
		event.preventDefault()

		const { email, password } = this.state

		if (!this.props.loading) {
			this.props.registerWithEmailPassword(email, password)
			submitAnalyticsEvent(AnalyticsEventCategory.User, AnalyticsEventAction.Register)
		}
	}
}



