import React from 'react'

export default function nl2br(rawText: string) {
	return (
		<React.Fragment>
			{rawText.split('\n').map((item, key) => {
				return (
					<span key={key}>
						{item}
						<br />
					</span>
				)
			})}
		</React.Fragment>
	)
}



