import handleActions from '../utils/handleActions'
import { SET_PRODUCTS, SetProductsAction, UNSET_PRODUCTS } from '../actions/products'

export interface IProduct {
	id: string
	name: string
	shortName: string
	volume: number
	price: number
	image: string
	ingredients: string[]
	color: string
	// @TODO: more to come
}

export interface IProductsState {
	loading: boolean
	items: { [id: string]: IProduct }
}

const initialState: IProductsState = {
	loading: true,
	items: {},
}

// tslint:disable:function-name
export default handleActions(
	{
		[SET_PRODUCTS](state, { payload }: SetProductsAction) {
			return {
				...state,
				loading: false,
				items: { ...payload },
			}
		},
		[UNSET_PRODUCTS](state) {
			return {
				...state,
				loading: true,
				items: {},
			}
		},
	},
	initialState,
)



