import axios, { AxiosResponse, CancelTokenSource, AxiosInstance } from 'axios'
import { Store } from 'react-redux'
import { IAddresses } from '../actions/addresses'
import { IProducts } from '../actions/products'
import formatProductPayload from './formatProductPayload'
import formatPromotionPayload from './formatPromotionPayload'
import { IPromotions } from '../actions/promotions'
import { logout } from '../actions/user'
import { IState } from '../reducers'
import { addNotification } from '../actions/notifications'
import { NotificationSeverity } from '../reducers/notifications'
import { IDate } from '../models/date'
import { IAddress } from '../reducers/addresses'
import { IBasketItems } from '../actions/basket'
import { IDeliveryWindow } from '../actions/orders'
import formatDateToLabel from './formatDateToLabel'
import { IOrders } from '../models/order'
import { setOffline, setOnline } from '../actions/general'

class Api {
	private cancelSource: CancelTokenSource
	private axios: AxiosInstance
	private token: string = ''
	private store: Store<IState>

	constructor() {
		this.createCustomAxios()
	}

	// @TODO: any
	public setStore(store: Store<any>) {
		this.store = store as Store<IState>
		const refresh = () => {
			const state = store.getState()
			const { token } = state.user
			if (token !== this.token) {
				if (token) {
					this.onUserLogin(token)
				} else {
					this.onUserLogout()
				}
			}
		}
		refresh()
		store.subscribe(refresh)
	}

	private onUserLogin(token: string) {
		this.setAuthorizationToken(token)
	}

	private onUserLogout() {
		this.unsetAuthorizationToken()
		this.cancelSource.cancel('User logged out.')
		this.createCustomAxios()
	}

	private createCustomAxios() {
		this.cancelSource = axios.CancelToken.source()
		this.axios = axios.create({
			baseURL: 'https://cors-anywhere.herokuapp.com/https://api.beta.goodlok.cz',
			cancelToken: this.cancelSource.token,
		})
		this.axios.interceptors.response.use(
			this.responseInterceptorFulfilled,
			this.responseInterceptorRejected,
		)
	}

	public async loginWithEmailPassword(email: string, password: string) {
		return this.axios
			.post('signin', {
				email,
				password,
			})
			.then(this.handleUserResponse)
	}

	public async registerWithEmailPassword(email: string, password: string) {
		return this.axios
			.post('signup', {
				email,
				password,
			})
			.then(this.handleUserResponse)
	}

	public async requestUser() {
		return this.axios.get('user').then(this.handleUserResponse)
	}

	public async passwordReset(email: string) {
		return this.axios
			.post('password/reset', {
				email,
			})
			.then(() => {
				return null
			})
	}

	public async requestAddresses() {
		return this.axios.get('user/addresses').then((response) => {
			const addresses: IAddresses = {}
			response.data.forEach((address: any) => {
				addresses[address.id] = {
					address: address.raw,
					id: address.id,
					title: address.title,
				}
			})
			return addresses
		})
	}

	public async requestOrders() {
		return this.axios.get('user/orders').then((response): IOrders => {
			const orders: IOrders = {}
			response.data.forEach((order: any) => {
				orders[order.id] = {
					id: order.id,
					addressTitle: order.addressTitle,
					deliverAt: order.deliverAt,
					deliveredAt: order.deliveredAt,
					products: order.products,
					repeatOn: order.repeatOn,
				}
			})
			return orders
		})
	}

	public async addAddress(title: string, address: string) {
		return this.axios
			.post('user/address', { title, raw: address })
			.then((response) => {
				return {
					address,
					title,
					id: response.data.id,
				}
			})
	}

	public async removeAddress(address: IAddress) {
		return this.axios.delete(`user/address/${address.id}`).then(() => {
			return null
		})
	}

	public async requestProducts() {
		return this.axios.get('products/offer').then((response) => {
			const products: IProducts = {}
			response.data.forEach((product: any) => {
				products[product.id] = formatProductPayload(product)
			})
			return products
		})
	}

	public async requestPromotions() {
		return this.axios.get('promotions').then((response) => {
			const promotions: IPromotions = {}
			response.data.forEach((promotion: any) => {
				promotions[promotion.id] = formatPromotionPayload(promotion)
			})
			return promotions
		})
	}

	public async order(items: IBasketItems, address: IAddress, date: IDate, paymentMethod: string) {
		return this.axios.post('order', {
			paymentMethod,
			items: Object.values(items).map((item) => ({
				productID: item.product.id,
				quantity: item.quantity,
			})),
			deliveryAddressId: address.id,
			deliveryDate: date.value,
		}).then((response) => {
			return {
				gatewayRedirectLink: (response.data && response.data.gatewayRedirectLink) || null,
			}
		})
	}

	public async removeCard() {
		return this.axios.post('user/remove-card')
			.then(() => {
				return null
			})
	}

	public async requestNextDeliveryWindow() {
		return this.axios.get('delivery/next')
			.then((response): IDeliveryWindow => {
				const { data } = response
				return {
					duration: data.duration,
					query: data.query,
					date: {
						id: `window-${data.nextWindow}`,
						label: formatDateToLabel(data.nextWindow),
						value: data.nextWindow,
					},
				}
			})
	}

	private handleUserResponse = (response: AxiosResponse) => {
		const {
			id,
			email,
			token,
			cardIssuer,
			cardLast4,
		} = response.data
		return {
			id,
			email,
			token,
			cardIssuer,
			cardLast4,
		}
	}

	private setAuthorizationToken(token: string) {
		this.token = token
		this.axios.defaults.headers.common['Authorization'] = `Bearer ${token}`
	}

	private unsetAuthorizationToken() {
		delete this.axios.defaults.headers.common['Authorization']
	}

	private responseInterceptorFulfilled = (response: AxiosResponse<any>) => {
		const state = this.store.getState()
		if (state.general.offline) {
			this.store.dispatch(setOnline())
		}
		return response
	}

	private responseInterceptorRejected = (error: any) => {
		if (axios.isCancel(error)) {
			throw error
		}

		const state = this.store.getState()

		let message: string = 'Error ' + error.toString()
		if (error.response && error.response.data && error.response.data.error) {
			message = error.response.data.error
		}

		if (error.response) {
			switch (error.response.status) {
			case 401:
				if (state.user.loggedIn) {
					this.store.dispatch(logout())
					this.store.dispatch(addNotification(`You have been logged out. ${message}`, NotificationSeverity.Warning))
				}
				break
			default:
				this.store.dispatch(addNotification(message, NotificationSeverity.Danger)) // @TODO: remove
			}
			console.info(error.response)
		} else {
			this.store.dispatch(setOffline())
		}
		throw error
	}
}

export default new Api()



