import React from 'react'
import styles from './styles.sass'

export interface ICollapseFromTopActionProps {
	onClick: () => void
}

export default class CollapseFromTop extends React.Component<ICollapseFromTopActionProps> {

	render() {
		const { onClick } = this.props

		return (
			<button aria-label="Collapse" className={styles.wrapper} role="button" onClick={onClick}>
				<i className="fas fa-chevron-down"></i>
			</button>
		)
	}
}



