import React from 'react'
import AddressEditor from '../../containers/AddressEditorContainer'
import Modal from '../Modal'
import translate from '../../utils/translate'

export interface IAddAddressModalOwnProps {
	isActive: boolean
	onClose: () => void
}

export default class AddAddressModal extends React.Component<
	IAddAddressModalOwnProps
> {
	render() {
		const {
			isActive,
			onClose,
		} = this.props

		return (
			<Modal
				isActive={isActive}
				onClose={onClose}
				title={translate('addresses.addAddress')}
			>
				<AddressEditor
					onDone={onClose}
				/>
			</Modal>
		)
	}
}



