import { IProducts } from '../actions/products'
import formatProductPayload from './formatProductPayload'
import { IPromotion } from '../reducers/promotions'

export default function formatPromotionPayload(data: any): IPromotion {
	const products: IProducts = {}
	data.products.forEach((product: any) => {
		products[product.id] = formatProductPayload(product)
	})
	return {
		products,
		id: data.id,
		intro: data.intro,
		name: data.name,
	}
}



