import React from 'react'
import { IBasketItems } from '../../actions/basket'
import styles from './styles.sass'
import BottomBar from '../BottomBar'
import BasketItems from '../BasketItems'

export interface IBasketPreviewOwnProps {
	isActive: boolean
}
export interface IBasketPreviewProps {
	items: IBasketItems
}

export interface IBasketPreviewActionProps {
	onOpenDetail: () => void
}

interface IProps extends IBasketPreviewProps, IBasketPreviewActionProps, IBasketPreviewOwnProps {}

export default class BasketPreview extends React.Component<
	IProps
> {
	render() {
		const { items, onOpenDetail, isActive } = this.props

		return (
			<BottomBar isActive={isActive}>
				<div className={styles.items}>
					<BasketItems items={items} />
				</div>
				<div className={styles.continue}>
					Proceed
					<i className="fas fa-arrow-right"></i>
				</div>
				<button
					className={styles.action}
					role="button"
					aria-label="Show basket"
					onClick={onOpenDetail}
				/>
			</BottomBar>
		)
	}
}



