import { connect } from 'react-redux'
import { IState } from '../reducers'
import Anonymous, { IAnonymouseActionProps } from '../components/Anonymous'
import { autoLoginWithEmailPassword } from '../actions/user'

const mapStateToProps = (_: IState) => ({})
const mapDispatchToProps: IAnonymouseActionProps = {
	autoLoginWithEmailPassword,
}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(Anonymous)



