import { connect } from 'react-redux'
import { IState } from '../reducers'
import BasketTuner, { IBasketTunerActionProps } from '../components/BasketTuner'
import {
	addToBasket,
	removeFromBasket,
} from '../actions/basket'

const mapStateToProps = (state: IState) => ({
	items: state.basket,
})
const mapDispatchToProps: IBasketTunerActionProps = {
	addToBasket,
	removeFromBasket,
}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(BasketTuner)



