import { connect } from 'react-redux'
import { addAddress, editAddress } from '../actions/addresses'
import Addresses, {
	IAddressEditorActionProps, IAddressEditorOwnProps,
} from '../components/AddressEditor'
import { IState } from '../reducers'

const mapStateToProps = (state: IState, ownProps: IAddressEditorOwnProps) => ({
	...ownProps,
	loading: state.addresses.loading,
})
const mapDispatchToProps: IAddressEditorActionProps = {
	addAddress,
	editAddress,
}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(Addresses)



